
import XHeader from 'vux/src/components/x-header/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import Popup from 'vux/src/components/popup/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import InlineXNumber from 'vux/src/components/inline-x-number/index.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import Badge from 'vux/src/components/badge/index.vue'
import Loading from 'vux/src/components/loading/index.vue'


import $ from "jquery";

export default {
name: "godosInfo",
components: {
XHeader,
ViewBox,
Swiper,
Popup,
XSwitch,
Group,
Cell,
CheckerItem,
Checker,
InlineXNumber,
FlexboxItem,
Flexbox,
Badge,
Loading
},
data() {
return {
show: true,
add: [],
isSpec: false,
spec: null,
goods_count: 1,
goods_id: '',
bag_num: 0,
goods_info: {
goods_id: "",
goods_title: "",
goods_money: 0,
goods_stock: 0,
spec: {
"map": [],
paramList: []
},
img_list: [],
is_collection: false
},
mapList: [],
headImgList: []
};
},

methods: {

toIndemnity() {
console.log("");
this.$router.push({
path: "/indemnity/indemnity"
});
},
onSpecClick() {},
setSpec() {
var spec = this.spec;

for(var i = 0; i < this.goods_info.spec.paramList.length; i++) {
var item = this.goods_info.spec.paramList[i];
var is = true;
for(var x in item.params) {
if(spec[x] == undefined) {
is = false;
} else {
if(item.params[x] != spec[x].title) {
is = false;
}
}
}
if(is) {
spec.money = item.money;
spec.stock = item.stock;
}

}
this.spec = spec;

},
addShopBag() {
this.setSpec();
console.log(this.spec);
if(!this.spec['money']) {
this.$vux.toast.show({
text: "",
type: "text",
position: "middle"
});
return;
}




this.$post(
"bag/add", {
add: {
spec: this.spec,
goods_id: this.goods_info.goods_id,
goods_count: this.goods_count,
}
},
res => {
if(res.res >= 1) {
this.$vux.toast.show({
text: "",
type: "text",
position: "middle"
});
this.isSpec = false;
this.bag_num = res.bag_num;
}
if(res.res < 0) {
this.$vux.toast.show({
text: "",
type: "text",
position: "middle"
});
}

}
);

},
collection() {

this.$post(
"collection/change", {
goods_id: this.goods_id
},
res => {
console.log(res);
if(res.res >= 1) {
if(res.msg == 1) {
this.goods_info.is_collection = true;
this.$vux.toast.show({
text: "",
type: "text",
position: "middle"
});
}
if(res.msg == 0) {
this.goods_info.is_collection = false;
this.$vux.toast.show({
text: "",
type: "text",
position: "middle"
});
}
}

}
);

},
update() {
console.log(this.$route.params);

this.$get(
"goods/get", {
goods_id: this.goods_id
},
res => {

this.show = false;
if(res.res >= 1) {

this.bag_num = res.bag_num;
this.goods_info = res.msg;

this.spec = {};
for(var i = 0; i < this.goods_info.spec.spec; i++) {
this.spec[this.goods_info.spec.spec[i].title] = [];
}

for(var i = 0; i < this.goods_info.img_list.length; i++) {
this.goods_info.img_list[i].img = this.$getUrl(this.goods_info.img_list[i].src);
}
this.headImgList = this.goods_info.img_list;
console.log(res);
}
if(res.res < 0) {

}
}
);
},
},

mounted() {
if(!this.$route.params["goods_id"]) {
if(!localStorage.goods_id) {
this.$router.go(-1);
return;
} else {
this.goods_id = localStorage.goods_id;
}
} else {
this.goods_id = this.$route.params["goods_id"];
}
localStorage.goods_id = this.goods_id;
this.update();
},
watch: {
spec() {
this.setSpec();
}
}
};



